<template lang="pug">
    v-card 
        v-toolbar
            v-toolbar-title 搬砖交易对管理
            v-spacer
            v-btn(icon @click="refresh")
                v-icon mdi-refresh

        v-list
            bprunstatuslistitem(
                v-for="ex in exConfigs"
                :key="ex.FileName"
                :fileName="ex.FileName"
                :status="ex.Status"
                :amount="ex.Amount"
            )
            

</template>

<script>
import * as api from "@/apis/wswebapi"
import { get} from 'vuex-pathify'
export default {
    data: () => ({
    exConfigs: [],
    ExName:"WKJ"
    }),
    computed:{
        isLogin:get('user/islogin'),
        exbotisexpire:get('user/exbotisexpire'),
    },
    methods:{
        refresh(){

            if (this.exbotisexpire){
                this.$store.dispatch("msglog/push_msg",{message:"软件已过期，请联系管理员",type:"error"});
                return
            }

            api.BPGetAll().then(res=>{
                console.log(res)
                if (res.code==200){
                    let exconfigs=[];
                    for (var k in res.data){
                        exconfigs.push({FileName:k,Status:res.data[k]});
                    }
                    this.exConfigs=exconfigs;
                }else{
                    this.$store.dispatch("msglog/push_msg",{message:res.msg+res.error,type:"error"});
                }
                
            })
        }
    },

    components: {
      bprunstatuslistitem: () => import('@/views/brickpicker/bpComponents/runstatuslistitem'),
    },
}
</script>